<template>
  <div class="login">
    <div
      class="w-full tabTop from-pink-300 to-lightBlue-400 text-white font-serif font-bold text-xl text-center leading-10 rounded-b-3xl shadow-sm"
    >
      <i class="icon iconfont text-xl mr-1">&#xe70d;</i>
      登录
    </div>
    <div class="pt-10">
      <van-form @submit="login" ref="form">
        <div class="username">
          <van-field
            v-model="form.phone"
            name="phone"
            class="flex items-center px-10"
            placeholder="请输入手机号"
            :rules="rules.phone"
          >
            <template #left-icon>
              <i class="icon iconfont text-2xl mr-5">&#xe70a;</i>
            </template>
          </van-field>
        </div>
        <div class="password">
          <van-field
            v-model="form.code"
            name="password"
            :rules="rules.code"
            type="password"
            class="flex items-center pl-10 pr-8"
            placeholder="请输入验证码"
          >
            <template #left-icon>
              <i class="icon iconfont text-2xl mr-5">&#xe709;</i>
            </template>
            <template #button>
              <van-button color="#ededed" size="small" round type="primary"
                ><span style="color:#666">发送验证码</span></van-button
              >
            </template>
          </van-field>
        </div>
      </van-form>
    </div>
    <div class="text-center mt-20">
      <button
        @click="go"
        class="w-3/4 bg-gradient-to-r from-lightBlue-500 to-indigo-700 h-8 rounded-2xl h-11 focus:outline-none ring-purple-600 focus:ring-2 text-white focus:ring-offset-2"
      >
        登录
      </button>
    </div>
  </div>
</template>

<script>
// 导入网络请求的方法
import { sendLogin } from '../../api/login.js'
import { setToken } from '../../utils/token.js'
import { mapMutations } from 'vuex'
import { getUserInfo } from '@/api/userInfo'
export default {
  data () {
    return {
      form: {
        phone: '',
        code: '246810'
      },
      rules: {
        phone: [
          { required: true, message: '手机号不能为空', trigger: 'onBlur' },
          {
            pattern: /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/,
            message: '手机号不合法',
            trigger: 'onBlur'
          }
        ],
        code: [
          { required: true, message: '验证码不能为空', trigger: 'onBlur' },
          {
            pattern: /\d{4}/,
            message: '验证码长度只能为4',
            trigger: 'onBlur'
          }
        ]
      }
    }
  },
  methods: {
    // 登录
    async login () {
      // 添加登录状态
      this.$toast({
        type: 'loading',
        message: '正在登录...'
      })
      try {
        const res = await sendLogin({
          mobile: this.form.phone,
          code: this.form.code
        })
        console.log(res)
        // 设置token
        setToken(res.data.token)
        // 设置状态
        this.setUserState(true)
        // 获取用户信息
        const msg = await getUserInfo()
        // 保存用户信息到vuex
        this.setUserInfo(msg.data)
        // 跳转
        this.$router.push('/user')
        // 清除toast
        this.$toast.clear()
      } catch (error) {
        console.log(error)
      }
    },
    ...mapMutations(['setUserState', 'setUserInfo']),
    go () {
      this.$refs.form
        .validate(['phone', 'password'])
        .then(() => {
          this.login()
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>
